body {
  margin: 0;
  background-color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-intro {
 color: rgb(219, 169, 136);
 width: 70%;
 height: 370px;
 font-family: 'Josefin Sans', sans-serif;
 text-align: left;
 margin: 0px auto;
 padding-top: 120px;
 padding-bottom: 220px;
 
}
.name {
  color: rgb(217, 124, 80);
  font-family: 'Yeseva One', cursive;
}
.button {
  text-transform: capitalize;
  border-radius: 25px;
}

.button:hover {
  background-color: rgb(217, 124, 80) !important;
  color: rgb(22, 44, 58) !important;
  border-color: rgb(217, 124, 80);
}


@media screen and (max-width: 850px) {
  .header-intro {
    color: rgb(219, 169, 136);
    width: 100%;
    height: 300px;
    border-radius: 0px;
    font-family: 'Josefin Sans', sans-serif;
    text-align: center;
    padding-top: 50px;



   }
   .intro-image {
    display: none;
    width: 60%;
    position: unset;
    left: 50%

   }
   .landing-header{
     display: block;
   }
}
@media screen and (min-width: 1440px) {
  .header-intro {
    color: rgb(219, 169, 136);
    width: 70%;
    height: 370px;
    font-family: 'Josefin Sans', sans-serif;
    text-align: left;
    margin: 0px auto;
    padding: 120px 0px;
    
   }
   .intro-image {
    display: none;
    width: 60%;
    position: unset;
    left: 50%

   }
   .landing-header{
     display: block;
   }
}
.about-component {
  padding-top: 350px;
  padding-bottom: 150px;
  display: inline-block;
}
.quote {
  color: rgb(217, 124, 80);
  font-family: "Yeseva One", cursive;
}
.speaker {
  font-family: "Josefin Sans", sans-serif;
  color: rgb(219, 169, 136);
}
.first-paragraph {
  width: 200%;
  font-family: "Josefin Sans", sans-serif;
  color: rgb(219, 169, 136);
  margin-left: 50px;
  font-size: 24px;
  text-align: left;
}
.first-container {
  display: flex;
  align-items: flex-start;
}
.second-paragraph {
  width: 50%;
  font-family: "Josefin Sans", sans-serif;
  color: rgb(219, 169, 136);
  padding-right: 50px;
  text-align: left;
  font-size: 24px;
  float: right;
}

.me-image {
  width: 50%;
  height: 45%;
  margin-top: 40px;
  margin-right: 20px;
  margin-left: 80px;
  position: relative;
  border-radius: 20px;
}
.second-container {
  display: flex;
  align-items: flex-start;
}
.friends-image {
  float: left;
  width: 30%;
  height: 20%;
  margin-top: 20px;

  border-radius: 20px;
  position: relative;
  margin-left: 50px;
  /* display: none; */
}
@media screen and (max-width: 850px) {
    .about-component {
        padding: 350px 0px;
        display: block;
      }

    .first-container {
        display: unset;
        align-items: unset;
    }

    .first-paragraph {
        color: rgb(219, 169, 136);
        width: 100%;
        border-radius: 0px;
        margin-left: 0px;
        font-family: "Josefin Sans", sans-serif;
        text-align: center;
        font-size: 16px;
    }

    .friends-image {
        height: 50%;
        border-radius: 20px;
        position: unset;
        float: unset;
        display: none;
        margin: unset;
    }

    .me-image {
        width: 60%;
        height: 60%;
        border-radius: 20px;
        position: unset;
        float: unset;
        margin: unset;
    }

    .second-container {
        display: unset;
        align-items: unset;
    }

    .second-paragraph {
        color: rgb(219, 169, 136);
        width: 100%;
        border-radius: 0px;
        margin-left: 0px;
        font-family: "Josefin Sans", sans-serif;
        text-align: center;
        font-size: 16px;
        padding-right: 0px;
    }

    .speaker {
        font-family: "Josefin Sans", sans-serif;
        color: rgb(219, 169, 136);
    }

    .quote {
        color: rgb(217, 124, 80);
        font-family: "Yeseva One", cursive;
    }
}

